import {
  Component,
  getAssetPath,
  h,
  Host,
  /*Listen,*/ Prop,
} from '@stencil/core';
import { RangeValue } from '../../../components/ionic/range/utils/range.interface';
import { TpFontZoomSize } from './utils/font-zoom.enum';

@Component({
  tag: 'tp-font-zoom',
  styleUrl: 'font-zoom.scss',
  assetsDirs: ['assets'],
  scoped: true,
})
export class TpFontZoom {
  private fontUpIcon = getAssetPath('./assets/tp-type-minus.svg');
  private fontDownIcon = getAssetPath('./assets/tp-type-plus.svg');

  /**
   * todo
   */
  @Prop() emitter!: { emit: (value: TpFontZoomSize) => void };

  /**
   * todo
   */
  @Prop({ mutable: true }) value: TpFontZoomSize = TpFontZoomSize.XS;

  readonly min: number = 1;
  readonly max: number = 5;
  readonly step: number = 1;

  fontSizeToValue = (fontSize: TpFontZoomSize): RangeValue => {
    switch (fontSize) {
      case TpFontZoomSize.XXXS:
        return 1;
      case TpFontZoomSize.XXS:
        return 2;
      case TpFontZoomSize.XS:
        return 3;
      case TpFontZoomSize.SM:
        return 4;
      case TpFontZoomSize.MD:
        return 5;
    }
  };

  onRangeChange = (rangeValue: RangeValue): void => {
    if (this.emitter) {
      switch (rangeValue) {
        case 1:
          this.value = TpFontZoomSize.XXXS;
          break;
        case 2:
          this.value = TpFontZoomSize.XXS;
          break;
        case 3:
          this.value = TpFontZoomSize.XS;
          break;
        case 4:
          this.value = TpFontZoomSize.SM;
          break;
        case 5:
          this.value = TpFontZoomSize.MD;
          break;
        default:
          this.value = TpFontZoomSize.XS;
      }

      this.emitter.emit(this.value);
    }
  };

  // fix para conflito com popover API do chrome
  // pode remover depois de migração pro ionic 7
  // @Listen('ionPopoverDidPresent', { target: 'body' })
  // fixPopover() {
  //   const popover = document.querySelector('med-font-zoom');

  //   if (!popover) return;

  //   if (popover?.hasAttribute('popover')) {
  //     popover.removeAttribute('popover');
  //   }

  //   popover.style.opacity = '1';
  // }

  render() {
    const {
      min,
      max,
      step,
      fontUpIcon,
      fontDownIcon,
      fontSizeToValue,
      onRangeChange,
    } = this;

    return (
      <Host from-stencil>
        <ion-range
          onIonChange={(ev) => onRangeChange(ev.detail.value)}
          min={min}
          max={max}
          step={step}
          value={fontSizeToValue(this.value)}
        >
          <ion-icon slot="start" src={fontUpIcon}></ion-icon>
          <ion-icon slot="end" src={fontDownIcon}></ion-icon>
        </ion-range>
      </Host>
    );
  }
}
